@import url("https://fonts.googleapis.com/css?family=Lato&display=swap");
:root {
    --primaryColor: #f45a63;
    --mainWhite: #fff;
    --mainGrey: #274b4b;
    --mainRed: #d50000;
    --mainGreen: #4caf50;
}
* {
    margin: 0;
    box-sizing: border-box;
}
body {
    background: #ddd;
    color: var(--mainGrey);
    font-family: "Lato", sans-serif;
}
h1 {
    text-align: center;
    text-transform: capitalize;
    margin: 50px 0px 0px 0px;
    color: #222;
    letter-spacing: 5px;
}
.App {
    margin: 0 auto;
    max-width: 1280px;
    width: 90%;
    position: relative;
    top: 100px;
    background: var(--mainWhite);
    padding-bottom: 2rem;
    border-radius: 2px;
}
.form-center {
    display: flex;
    padding: 0 1rem;
}
.form-group {
    padding: 1rem 0.75rem;
    flex: 0 0 50%;
}
label {
    display: block;
    color: var(--mainGrey);
    font-size: 1rem;
    text-transform: capitalize;
}

.info {
    flex: 0 0 60%;
    display: flex;
    justify-content: space-between;
}

.total {
    font-weight: 300;
    color: #fff;
    background-color: var(--primaryColor);
    border-radius: 2px;
    padding: 2px 5px;
}

.alert-danger {
    background: var(--mainRed);
}
.alert-success {
    background: var(--mainGreen);
}

@media screen and (min-width: 601px) {
    .App {
        width: 85%;
    }
}
@media screen and (min-width: 993px) {
    .App {
        width: 70%;
    }
    .info {
        flex: 0 0 50%;
    }
}
